import React , {Component} from 'react';
import {getAllBooking} from './axios-service';
import { Link ,BrowserRouter} from 'react-router-dom';
import { Table } from 'reactstrap';
import {deleteBooking} from './axios-service';

class HomeComponent extends Component{
    constructor(props){
        super(props);
        this.state={
            booking:[]
        }
    }
     
    componentDidMount(){
        getAllBooking().then((response)=>{
            this.setState({booking : response.data})
            console.log(response.data);
        }).catch(function(error){
            console.log(error);
        })
    }

    handleDelete(id){
        deleteBooking(id).then((res)=>{
            alert("Deleted");
        }).catch((error)=>{
            alert("Something went wrong");
        })
    }
    getUpdateDate(dateInResponse){
        var d = new Date(dateInResponse);
        var day = d.getDate();
        var month = d.getMonth();
        var year = d.getFullYear();
        return day+"/"+month+"/"+year;
    }

    render(){
        return(<div className="container" id="popu">
            <h1>All Bookings</h1>
            <div>
           <Table>
               <tr>
                   <th>ID</th>
                   <th>FirstName</th>
                   <th>LastName</th>
                   <th>RoomType</th>
                   <th>Price</th>
                   <th>Number Of Days</th>
                   <th>Date Of Booking</th>
                   <th>Payment Type</th>
                   <th>Update</th>
                   <th>Delete</th>
               </tr>
       {this.state.booking.map((booking,i)=>
           <tr key={i}><td>{booking.id}</td>
           <td>{booking.firstname}</td>
           <td>{booking.lastname}</td>
           <td>{booking.roomtype}</td>
           <td>{booking.price}</td>
           <td>{booking.noOfDays}</td>
           <td>{this.getUpdateDate(booking.dateOfBooking)}</td>
           <td>{booking.paymentType}</td>
           <td><button className="btn btn-primary">Update</button></td>
           <td><button className="btn btn-danger" onClick={()=>this.handleDelete(booking.id)}>Delete</button></td>
           </tr>
       )}
           </Table>
           <BrowserRouter>
           <Link to='/AddComponent'><button className="btn btn-success"> Add Bookings  </button></Link>
    </BrowserRouter>
       </div>
       <br/><br/>
        </div>);
    }
}

export default HomeComponent;