import React from 'react'
import { Slide } from 'react-slideshow-image';
import 'react-slideshow-image/dist/styles.css';
import items from '../shared/items.js'
import { Card, CardImg, CardBody, CardTitle, CardText, Button } from 'reactstrap';
import { Link } from 'react-router-dom';

const slideImages = [
    '/images/Baner_4.jpg',
    '/images/Baner_5.jpg',
    '/images/Baner_3.jpg',
  ];


  const properties = {
    duration: 5000,
    transitionDuration: 500,
    infinite: true,
    indicators: true,
    arrows: true,
    pauseOnHover: true,
    onChange: (oldIndex, newIndex) => {
      console.log(`slide transition from ${oldIndex} to ${newIndex}`);
    }
  }
  const Slideshow = () => {
    return (
      <div className="slide-container">
        <Slide {...properties}>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[0]})`,'height':'700px','width':'2000px',}}>
              <span><b>Luxurious Meeting Rooms with outstanding views. </b></span>
            </div>
          </div>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[1]})`,'height':'700px','width':'2000px'}}>
              <span><b>Yes!!!We are having see facing bedrooms. We want you to awake with naturally fresh views.</b></span>
            </div>
          </div>
          <div className="each-slide">
            <div style={{'backgroundImage': `url(${slideImages[2]})`,'height':'700px','width':'2000px'}}>
              <span><b>Today is your Birthday?? No problem , we are having celebration halls with all facilities.Happy Birthday!!</b></span>
            </div>
          </div>
        </Slide>
      </div>
    )
}
function RenderCard({item}){
    console.log(item);
    return(
        <Card style={{"height":"500px","width":"350px"}}>
            <CardImg src={item.image} height="300px" width="300px" alt={item.name}/>
            <CardBody>
                <CardTitle>{item.name}</CardTitle>
                <CardText>{item.description}</CardText>
                Rs. <h5>{item.price}</h5>
            </CardBody>
           
            <Link to="/AddComponent">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<Button className="btn btn-success">Book Now</Button></Link>
        </Card>
    );
}
function BanerComponent (props){
    console.log(items);
    return(
        <div>
            <div className="row">
                <div className="col-12 col-sm-12">
                <Slideshow/>
                </div>
            </div>
            <div className="container">
            <div className="row align-items-start">
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[0]}/>
                </div>
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[1]}/>
                </div>
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[2]}/>
                </div>
            </div>
            <br/>
            <div className="row align-items-start">
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[3]}/>
                </div>
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[4]}/>
                </div>
                <div className="col-12 col-md mt-1">
                    <RenderCard item={items[5]}/>
                </div>
            </div>
            <br/>
        </div>
        </div>
    )
}
export default BanerComponent;