import React , {Component} from 'react'
import { Form, Label, Input, Button } from 'reactstrap';
import { Link } from 'react-router-dom';
import {addBooking} from "./axios-service";

class AddComponent extends Component{
    constructor(props){
        super(props);
        this.addFormData = this.addFormData.bind(this);
    }
    addFormData(){
        var datenow = new Date();
        var price = 0;
        console.log(document.getElementById("noOfDays").value);
        if(document.getElementById("roomtype").value==="Single Bed Luxury"){
            price = 2000;
        }
        else if(document.getElementById("roomtype").value==="Double Bed Luxury"){
            price = 3000;
        }
        else if(document.getElementById("roomtype").value==="Single Bed Deluxe"){
            price = 3500;
        }
        else if(document.getElementById("roomtype").value==="Double Bed Deluxe"){
            price = 5000;
        }
        var data = {
            "firstname" : document.getElementById("firstname").value,
            "lastname" : document.getElementById("lastname").value,
            "roomtype" : document.getElementById("roomtype").value,
            "noOfDays" : document.getElementById("noOfDays").value,
            "dateOfBooking": datenow,
            "paymentType" : document.getElementById("paymentType").value,
            "price" : price
        }

        addBooking(data).then((res)=>{
            alert("Request for Booking is sent. We will call you shortly. / Call 9881238864");
            console.log(res);
        }).catch((error)=>{
            alert("Something went wrong");
            console.log(error);
        })

    }
    render(){
        return(<div className="container">
            <h1>Book your Room</h1>
            <Form>
                <Label for="firstname"><b>First Name : </b></Label>
                <Input type="text" id="firstname"/>
                <Label for="lastname"><b>Last Name : </b></Label>
                <Input type="text" id="lastname"/><br/>
                <Label for="roomtype"><b>Choose Room Type :</b></Label>&nbsp;&nbsp;&nbsp;
                <select id="roomtype" className="form-control">
                    <option value="Single Bed Luxury">Single Bed Luxury</option>
                    <option value="Double Bed Luxury">Doubled Bed Luxury</option>
                    <option value="Single Bed Deluxe">Single Bed Deluxe</option>
                    <option value="Double Bed Deluxe">Double Bed Deluxe</option>
                </select><br/><br/>
                <Label for="noOfDays"><b>For how many days, you are staying : </b></Label>
                <Input type="number" id="noOfDays"/><br/>
                <Label for="paymentType"><b>Choose preferred payment type : </b></Label>&nbsp;&nbsp;&nbsp;
                <select id="paymentType" className="form-control">
                    <option value="Cash">Cash</option>
                    <option value="Internet Banking">Internet Banking</option>
                    <option value="Phone Banking">Phone Banking</option>
                    <option value="Other">Other</option>
                </select><br/><br/>
                <Button className="btn btn-success" onClick={this.addFormData}>Confirm Booking</Button>&nbsp;&nbsp;&nbsp;
                <Link to="/home"><Button className="btn btn-danger">Cancel</Button></Link>
                <br/><br/><br/>
            </Form>
        </div>);
    }
}

export default AddComponent;