import axios from 'axios'

export function getAllBooking(){
    return axios.get("https://5f7d8c64834b5c0016b063e7.mockapi.io/booking");
}

export function addBooking(booking){
    return axios.post("https://5f7d8c64834b5c0016b063e7.mockapi.io/booking",booking);
}

export function deleteBooking(id){
    return axios.delete("https://5f7d8c64834b5c0016b063e7.mockapi.io/booking/"+id);
}