import React, { Component } from 'react';
import { Navbar, NavbarBrand,Nav,Collapse,NavbarToggler,NavItem, Jumbotron, Button,Modal,ModalHeader,ModalBody,Form,FormGroup,Label,Input } from 'reactstrap';


class Header extends Component {
    constructor(){
        super();
        this.state={
            isNavOpen : false,
            isModelOpen : false
        }
        this.toggleNav=this.toggleNav.bind(this);
        this.toggleModal=this.toggleModal.bind(this);
        this.handleLogin = this.handleLogin.bind(this);
    }
    toggleNav(){
        this.setState({isNavOpen:!this.state.isNavOpen})
    }
    toggleModal(){
        this.setState({isModalOpen:!this.state.isModalOpen})
    }
    handleLogin(event) {
        this.toggleModal();
        console.log("AAAAAAAAAAAAAAAAAAAAA");
        console.log(this.username.value+"////"+this.password.value);
        if(this.username.value==="mndeshmukh12@gmail.com"&& this.password.value==="manish"){
            alert("Logged In Successfully");
            window.location.href="/home";
        }
        else{
            alert("Incorrect Username / Password");
        }
        event.preventDefault();
    }
  render() {
    return(
    <React.Fragment>
      <div  id="header1">
      <Navbar dark expand="md">
        <div className="container">
        <br></br>
            <NavbarToggler onClick={this.toggleNav}></NavbarToggler>
            <NavbarBrand className="mr-auto" href="/"><img src="images/HotelLogo.png" height="100" width= "100" alt="Hotel Nakshtra In"></img></NavbarBrand>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
            <Collapse isOpen={this.state.isNavOpen} navbar>
            <Nav navbar>
            &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <NavItem>
                <span className="fa fa-home fa-lg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="#"><h5>Home</h5></a></span>
                </NavItem>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <NavItem>
                <span className="fa fa-info fa-lg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="#"><h5>About Us</h5></a></span>
                </NavItem>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <NavItem>
                <span className="fa fa-list fa-lg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="#"><h5>Offers/Discounts</h5></a></span>
                </NavItem>
                &nbsp;&nbsp;&nbsp;&nbsp;&nbsp;
                <NavItem>
                <span className="fa fa-address-card fa-lg">&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;<a href="#"><h5>Contact Us</h5></a></span>
                </NavItem>
            </Nav>
            <Nav className="ml-auto" navbar>
                <NavItem>
                    <button className="btn btn-primary" onClick={this.toggleModal}>Login</button>
                </NavItem>
            </Nav>
            </Collapse>
        </div>
      </Navbar>
      </div>
      <Jumbotron  style={{'backgroundImage':`url(/images/Baner_2.jpg)`}}>
           <div className="container" >
               <div className="row row-header">
               <div className="col-12 col-sm-5">
                   </div>
                   <div className="col-12 col-sm-6">
                       <h1><b>Hotel Nakshtra In</b></h1>
                       <p><b>We take inspiration from the World's best hotels, and create a unique fusion experience. Incredible offers are there!</b></p>
                   </div>
               </div>
           </div>
       </Jumbotron>
       <Modal isOpen={this.state.isModalOpen} toggle={this.toggleModal}>
           <ModalHeader toggle={this.toggleModal}>Log In</ModalHeader>
           <ModalBody>
           <Form onSubmit={this.handleLogin}>
                            <FormGroup>
                                <Label htmlFor="username">Username</Label>
                                <Input type="text" id="username" name="username"
                                    innerRef={(input) => this.username = input} />
                            </FormGroup>
                            <FormGroup>
                                <Label htmlFor="password">Password</Label>
                                <Input type="password" id="password" name="password"
                                    innerRef={(input) => this.password = input}  />
                            </FormGroup>
                            <FormGroup check>
                                <Label check>
                                    <Input type="checkbox" name="remember"
                                    innerRef={(input) => this.remember = input}  />
                                    Remember me
                                </Label>
                            </FormGroup>
                            <Button type="submit" value="submit" color="primary">Login</Button>
                        </Form>
           </ModalBody>
       </Modal>
    </React.Fragment>
    );
  }
}

export default Header;